<template>
  <div class="scanresult">
        <vue-headful
            title="0xsp Mongoose | Linux Categories"
            description=""
        />        
    <div class="row">
      <div class="col-md-4" v-for="category in categories" :key="category.cat_id">
      <card class="category">
      <div>
        <router-link :to="'/scanresult/' + category.cat_id + '/' + id">
        <div class="author text-center">
          <img width="80px" class="border-white" :src="'/icons/' + category.cat_icon" alt="...">
          <h4 class="title">{{ category.cat_name }} </h4>
        </div>
        </router-link>
      </div>
    </card>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      categories: {},
      id : this.$route.params.id, 
    };
  },
  methods: {
    
    async getCategories() {
        let id = this.$route.params.id;
        this.axios.get('/mydashboard/scanresult/2/' + id).then(response => (this.categories = response.data.rows));
    },
  },
  created() {
      this.getCategories();
  }
};
</script>

<style scoped></style>
